@import url(https://fonts.googleapis.com/css?family=Lato:400,700&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "line-awesome";
  src: url("/line-awesome/line-awesome/fonts/line-awesome.ttf");
}

 html, body {
  height: 100%;
  width: 100vw;
  min-height: 0;
  font-size: 10px;
  font-family: "line-awesome";
}

.App {
  /*text-align: center;*/
  display: flex;
  flex-direction: row;
  width: 100%;
  /*height: 100vh;*/
  /*overflow: hidden;*/
  min-height: 0; 
  height: 100%;
}

.Overview {
  width: 100%;
  display: flex;
  flex-direction: column;
  overflow: scroll;
  height: 100%;
  background-color: #ecf0f1;
}

.toolbar__heading {
  margin-top: 0;
}

.overview__toolbar__button:focus {
  outline: none;
}

.overview__toolbar__button--group:after {
  position: absolute;
  content: " ";
  border: 4px dashed white;
  border-radius: 5px;
  width: 18%;
  height: 60%;
}

.toolbar__heading {
  font-size: 3rem;
  font-family: sans-serif;
  text-align: left;
}

.Sidebar {
  width: 20%;
  max-width: 300px;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  list-style-type: none;
  padding: 0;
  margin: 0;
  overflow-y: scroll;
  min-width: 300px;
  position: relative;
  z-index: 1000;
  justify-content: flex-start;
  align-items: center;
  background-color: white; 
  border-radius: 5px 0 0 5px;
  overflow: scroll;
  box-shadow: -2px 0px 5px 0px rgba(120,120,120,1);
} 

.Sidebar__item {
  width: 100%;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0;
  height: 7.5vh;
  max-height: 80px;
  background-color: transparent;
  /*border-bottom: 1px solid #247EC2;*/
  box-sizing: border-box;
}

.fixture-button {
  width: 100%;
  height: 100%;
  padding: 0;
  background-color: transparent;
  border: 0;
  color: #212121;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  box-sizing: border-box;
}

.Sidebar__item:hover {
  cursor: pointer;
  background-color: #D7DBDC;
  box-sizing: border-box;
}

.fixture-button:hover {
  cursor: pointer;
}

.fixture-button:focus {
  outline: none;
}

.fixture-button * {
  margin: 5px 0;
  width: 50%;
  text-align: left;
}

.fixture-button__label {
  font-size: 1.4rem;
  width: 100%;
  text-align: left;
}

.editor {
  width: 100%;
  display: flex;
  flex-direction: row;
  height: 100vh; 
  min-height: 0;
  flex-grow: 1;
}

.editing-area {
  flex-grow: 1;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  /*padding: 40px;*/
  height: 100vh;
  min-height: 0;
  overflow: hidden; 
  min-height: 0;
}

.toolbar {
  /*position: fixed;*/
  z-index: 100;
  width: 100px;
  min-width: 100px;
  background-color: white;
  top: 0;
  left: 0;
  box-sizing: border-box;
  /*padding: 20px 0;*/
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  border-radius: 5px;
  box-shadow: 1px 0px 5px 0px rgba(120,120,120,1);
}

.toolbar__button-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.toolbar__button {
  background-color: transparent;
  border: none;
  font-weight: bold;
  /*margin-bottom: 50px;*/
  height: 10vh;
  max-height: 80px;
  color: #3C3C3C; 
  font-size: 3rem;
}

.toolbar__button:hover {
  cursor: pointer;
  border-left: 3px solid #0D5E9A;
  color: #0D5E9A;
}

.toolbar__button:focus {
  outline: none;
}

.toolbar__button--add {
  
}

.toolbar__button--back {
  margin-bottom: 50px;
  border-bottom: 2px solid #eee;
}

.risers-container {
  flex-grow: 1;
  width: 90%;
  margin: 0 auto;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: visible;
  box-sizing: border-box;
  margin: 10px auto;
  overflow-x: hidden;
  padding: 5% 0;
}

.scroll-button {
  border-radius: 5px;
  border: none;
  font-size: 3rem;
  color: grey;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
}

.scroll-button:hover {
  color: blue;
  cursor: pointer;
}

.scroll-button--right {
  position: fixed;
  z-index: 10000;
  right: 1.25%;
  top: 50%;
}

.scroll-button--left {
  position: fixed;
  z-index: 10000;
  left: 1.25%;;
  top: 50%;
}

.riser-wrapper {
  flex-grow: 1;
  background-color: #f7f7f7;
  overflow-y: scroll;
  background-color: #ecf0f1;
  padding: 30px;

}

.riser {
  display: flex;
  flex-direction: column-reverse;
  list-style-type: none;
  justify-content: center;
  flex-grow: 1;
  padding: 0;
  /*position: relative;*/
  /*z-index: 100;*/
  /*flex: 1 1 1000px;*/
  /*margin-bottom: 50px;*/
  overflow-y: scroll;
  margin: 0;
  box-sizing: border-box;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 1px 1px 5px 0px rgba(120,120,120,1);
  position: relative;
}

/*.riser-editing-area {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  overflow-y: hidden;
  position: relative;
  height: 100%;
}*/

.riser-list {
  display: flex;
  flex-direction: row;
  /*height: 70vh;*/
  list-style-type: none;
  padding: 0;
  justify-content: flex-start;
  /*height: 100%;*/
  box-sizing: border-box;
  /*margin-bottom: 20px;*/
  position: relative;
  top: 0;
  margin-top: 0;
  width: 100%;
  height: 100%;
  padding: 0 10px;
  /*top: 150px;*/
  align-self: flex-end;
  /*overflow-x: hidden;*/
  overflow-y: visible;
}

.floor-lines {
  width: 100%;
  position: absolute;
  top: 117px;
  box-sizing: border-box;
  overflow: hidden;
  /*margin-top: 10px;*/
  position: -webkit-sticky;
  position: sticky;
  left: 0;
  top: 0;
  height: 100%;
}

.floor-line {
  width: 100%;
  height: 100px;
  max-height: 100px;
  border-bottom: 1px solid lightgrey;
  box-sizing: border-box;
  max-height: 100px;
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
  position: relative;
  box-sizing: border-box;
}

.floor-line__label {
  margin: 0;
  margin-top: 1px;
  font-size: 1.5rem;
  color: #333;
  font-family: sans-serif;
  width: 75px;
  height: 100px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  position: absolute;
  z-index: 5000;
}

.riser__toolbar-wrapper {
  width: 100%;
  height: 150px;
  display: flex;
  flex-direction: row;
  /*position: absolute;*/
  overflow-x: hidden;
  min-height: 150px;
  top: 10px;
  z-index: 1000;
}

.label__text {
  margin: 0;
}

.floor {
  border-bottom: 2px solid grey;
  display: flex;
  align-items: flex-end;
  min-height: 120px;
  /*height: 7.5vh;*/
  /*height: auto;*/
  /*height: 100%;*/
  position: relative;
  overflow-y: auto;
  overflow-x: hidden;
  flex-shrink: 0;
  box-sizing: border-box;
  background-color: #F4F5F5;
}

.floor--active {
  background-color: white;
  box-shadow: 0px 0px 5px 2px rgba(150,150,150,1);
  position: relative;
  z-index: 2;
  border-radius: 5px;
  -webkit-transform: scaleY(1.02);
          transform: scaleY(1.02);
}

.floor--source {
  border-bottom: 2px solid red;
}

.floor__total-info {
  width: 100px;
  /*height: 100%; */
  min-width: 100px;
  display: flex;
  flex-direction: column;
  align-self: stretch;
  box-sizing: border-box;
}

.floor__main {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  /*border-left: 4px solid red;*/
  height: 100%;
  width: 100%;
  min-height: 150px;
  padding: 10px 0;
  box-sizing: border-box;
  position: relative;
}

.floor--cold-source .floor-fixtures:after { 
  border-top: 4px solid blue;
  position: absolute;
  content: "";
  height: 10px;
  top: -10px;
  width: 100%;
  left: -5px;
}

.floor--hot-source .floor-fixtures:before { 
  border-top: 4px solid red;
  position: absolute;
  content: "";
  height: 10px;
  top: 0px;
  width: 100%;
  left: 5px;
}

.floor--cold .floor__main:after {
  border-left: 4px solid blue;
  height: 100%;
  width: 100%;
  position: absolute;
  content: " ";
  left: -5px;
}

.floor--hot .floor__main:before {
  border-left: 4px solid red;
  height: 100%;
  width: 100%;
  position: absolute;
  content: " ";
  left: 5px;
}

.floor__label {
  width: 100%;
  margin: 0;
  /*margin-top: 20px;*/
  align-self: flex-end;
  text-align: right;
}

.floor__input {
  width: 100%;
  text-align: right;
}

.floor__toolbar {
  /*position: absolute;
  right: 0;
  top: 0;*/
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  /*height: 100%;*/
  padding: 5px;
  box-sizing: border-box;
  width: 7vw;
  max-width: 120px;
  min-height: 100px;
  align-self: stretch;
  z-index: 10;
}

.floor__toolbar__button {
  font-size: 2rem;
  background-color: transparent;
  border: none;
  color: #ACAFB0;
}

.floor__toolbar__button:hover {
  cursor: pointer;
  color: #0D5E9A;
}

.floor__toolbar__button--hot:hover {
  color: red;
}

.floor--cold-source .floor__toolbar__button--cold {
  color: blue;
}

.floor--hot-source .floor__toolbar__button--hot {
  color: red;
}

.floor__total-info {
  /*height: 100%;*/
  min-height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 0 10px;
  padding-bottom: 5px;
  width: 5vw;
  max-width: 150px;
}

.floor__total-info__helper {
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
  justify-content: center;
  flex-grow: 1;
}

.floor__total-info__values {
  margin: 0;
  /*margin-bottom: 20px;*/
  font-size: 1.4rem;
}

.floor__total-info__column {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;  
  max-height: 120px;
}

.toolbar__info {
  display: flex;
  flex-direction: column;
}

.toolbar__info__text {
  margin: 0;
  font-size: 1.5rem;
}

.toolbar__info__text--cold {
  color: #3867d6;
}

.toolbar__info__text--hot {
  color: #d63031;
}

.toolbar__info__text--total {
  color: #6c5ce7;
}

.floor-fixtures {
  display: flex;
  flex-direction: row;
  height: 100%;
  /*width: 100%;*/
  flex-grow: 1;
  flex-wrap: wrap;
  padding-left: 20px;
  padding-top: 10px;
  position: relative;
  min-height: 100px;
}

.fixture {
  /*height: 100%;*/
  height: 7.5vh;
  max-height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin: 0 10px;
}

.fixture * {
  margin: 0;
}

.fixture__label {
  font-size: 1.6rem;
} 

.fixture__info {
  font-size: 2rem;
}
.overview__toolbar__button {
	/*width: 8vw;*/
	height: 50px;
	font-size: 2rem;
	font-family: "Lato", sans-serif;
	/*box-shadow: none;	*/
	border-radius: 0;
	background-color: transparent;
	border: 0;
	color: #555;
}

.overview__toolbar__button:hover {
	cursor: pointer;
	color: #4d83fe;
}
.overview__toolbar {
	width: 100%;
	height: 5vh;
	max-height: 150px;
	display: flex;
	padding: 0 5%;
	box-sizing: border-box;
	justify-content: space-between;
	align-items: center;
	background-color: white;
	box-shadow: 0 3px 10px rgba(0,0,0,0.1);
}

.toolbar__heading {
	margin: 0;
	font-size: 3rem;
	font-family: "Lato", sans-serif;
}
.label__input::-webkit-inner-spin-button,
.label__input::-webkit-outer-spin-button {
	-webkit-appearance: none; 
  margin: 0; 
}

.label__form {
	width: 100%;
}

.label__input {
	/*border-radius: 5px;*/
	position: relative;
	border: none;
	border-bottom: 2px solid #4d83fe;
	box-sizing: content-box;
	height: 100%;
	width: 100%;
}
.label {
  height: 50px;
  width: 80%;
  margin: 0 auto;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-self: center;
  background-color: white;
  min-width: 50px;
  margin-bottom: 25px;
  margin-top: 25px;
  color: #4d83fe;
  font-family: "Lato", sans-serif;
  font-weight: bold;
}

.label__text {
  font-weight: 700;
  margin-right: 10px;
  display: flex;
  align-items: center;
  font-size: 3rem;
  border-bottom: 2px solid rgb(220,220,220);
  width: 100%;
  padding-bottom: 10px;
}
* {
  font-family: "Lato", sans-serif;
}

.riser-helper {
  width: 80%;
  height: 100%;
  margin: 0 auto;
  position: relative;
  /*top: 0;*/
  /*height: 100%;*/
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  overflow: visible;
  position: relative;
  font-family: "Lato", sans-serif;
}

.riser-text__wrapper {
  margin-bottom: 40px;
}

.riser__subheading {
  font-size: 2rem;
  font-weight: 700;
  margin: 0;
  color: #4D5253;
}

.riser__text {
  font-size: 1.5rem;
  margin: 10px 0 0 0;
  color: #8D9698;
}

.button--edit {
  width: 100%;
  height: 50px;
  margin: 0 auto;
  border: none;
  border-radius: 5px;
  position: absolute;
  bottom: 0;
  background-color: #4d83fe;
  font-size: 1.8rem;
  font-weight: bold;
  box-shadow: 0 2px 5px rgba(77,131,254, 0.5);
  display: flex;
  align-items: center;
  color: white;
  box-sizing: border-box;
  padding: 0 10%;
  justify-content: space-between;
}

.button--edit:hover {
  cursor: pointer;
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
  box-shadow: 0 4px 5px rgba(77,131,254, 0.5);
}

.button__arrow {
  margin: 0;
  background-color: #406CD0;
  border-radius: 100%;
  height: 30px; width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.riser-item__info {
	width: 100%;
	flex-grow: 1;
	box-sizing: border-box;
	padding: 20px 0;
}
.riser__toolbar {
	width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-sizing: border-box;
  position: -webkit-sticky;
  position: sticky;
}
.riser-item {
  width: 10vw;
  min-width: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  border-radius: 5px;
  /*top: 160px;*/
  background-color: white;
  margin-right: 40px;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
}
.riser-list--empty {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 5rem;
	box-sizing: border-box;
	margin: 0;
	color: rgb(200,200,200);
}

.toolbar__label {
	font-size: 3rem;
	text-align: center;
}
.floor__input {
	border: 0;
	background-color: transparent;
	border-bottom: 2px solid #4d83fe;
}

.floor__input::-webkit-inner-spin-button,
.floor__input::-webkit-outer-spin-button {
	-webkit-appearance: none; 
  margin: 0; 
}
.floor__total-info__values {
	font-size: 2rem;
	color: #555;
}
.fixture {
	display: flex;
	flex-direction: column;
	align-items: center;
	background-color: #4d83fe;
	padding: 0 20px;
	border-radius: 2px;
	height: 100%;
	color: white;
	box-shadow: 0px 2px 5px 1px rgba(100,100,100,1);
	margin-bottom: 20px;
	max-height: unset;
}

.fixture__info {
	background-color: #406CD0;
	padding: 15px;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 100%;
	font-size: 2.8rem;
}

.fixture__type {
	font-size: 1.5rem;
}
.floor-fixtures {
	height: 90%;
	box-sizing: border-box;
	padding: 20px 20px 0 20px;
}
.floor__main {
	height: 100%;
	min-height: 100%;
}
.floor {
	height: 200px;
}
